import React from 'react';

const Xp = (props) => {

    return (

            props.xp.map((item) => {
                    return (
                        <div key={item.id} className={item.className}>
                            <h4>{item.title}</h4>
                            <h5>{item.date}</h5>
                            <p>{item.description}</p>
                        </div>
                           
                        )
                    })
            );
            
};

export default Xp;