import React from 'react';
import { NavLink } from 'react-router-dom';

const NavBar = () => {
    return (
    <div className="sidebar">
        <div className="id">
            <div className="idContent">
                <img src="/media/avatar2.jpg" alt="profilePic" />
                <h3>Soufiane Masaif</h3>
            </div>
        </div>

        <div className="nav">
            <ul>
                <li>
                <NavLink exact to="/" activeClassName="navActive">
                    <i className="fas fa-home"></i>
                    <span> Home</span>
                </NavLink>
                </li>
                <li>
                <NavLink exact to="/Skills" activeClassName="navActive">
                    <i className="fas fa-users-cog"></i>
                    <span> Skills</span>
                </NavLink>
                </li>
                {/* <li>
                <NavLink exact to="/Portfolio" activeClassName="navActive">
                    <i className="fas fa-images"></i>
                    <span> Portfolio</span>
                </NavLink>
                </li> */}
                <li>
                <NavLink exact to="/Contact" activeClassName="navActive">
                    <i className="fas fa-address-book"></i>
                    <span> Contact</span>
                </NavLink>
                </li>
            </ul>
        </div>

        <div className="socialNetwork">
            <ul>
                <li>
                    <a href="https://github.com/XpecialOne" target="_blank" rel="noopener noreferrer">
                        <i className="fab fa-github"></i>
                    </a>
                </li>
                <li>
                    <a href="https://www.linkedin.com/in/soufiane-masaif-39940a1b3/" target="_blank" rel="noopener noreferrer">
                        <i className="fab fa-linkedin"></i>
                    </a>
                </li>
            </ul>

        </div>

        <div className="footer">
            <p>Powered by <b>React </b><i className="fab fa-react"></i></p>
            
        </div>

    </div>
    

    );
};

export default NavBar;