import React from 'react';
import NavBar from '../components/NavBar';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const Contact = () => {
    return (
        <div className="contact">
            <NavBar />
            <div className="contactContent">
                <div className="header"></div>
                <div className="contactBox">
                    <h1>Contact me</h1>
                    <ul>
                        <li>
                            <i className="fas fa-map-marker-alt"></i>
                            <span>Rabat-Morocco</span>
                        </li>
                        <li>
                            <i className="fas fa-mobile-alt"></i>
                            <CopyToClipboard text="+212615478223">
                                <span className="clickInput" onClick={() => alert('Phone number copied')}>+212 6 15 47 82 23</span>
                            </CopyToClipboard>
                        </li>
                        <li>
                            <i className="far fa-envelope"></i>
                            <CopyToClipboard text="sofiane.masaif@gmail.com">
                                <span className="clickInput" onClick={() => alert('Email address copied')}>sofiane.masaif@gmail.com</span>
                            </CopyToClipboard>
                        </li>

                    </ul>
                </div>
                <div className="socialNetwork">
                    <ul>

                        <a href="https://github.com/XpecialOne" target="_blank" rel="noopener noreferrer">
                            <i className="fab fa-github"></i>
                        </a>


                        <a href="https://www.linkedin.com/in/soufiane-masaif-39940a1b3/" target="_blank" rel="noopener noreferrer">
                            <i className="fab fa-linkedin"></i>
                        </a>

                    </ul>
                </div>

            </div>

        </div>
    );
};

export default Contact;