import React from 'react';

const Hobbies = () => {
    return (
        <div className="hobbies">
            <h3>Interests</h3>
            <ul>
                <li className="hobby"><i className="fas fa-cloud"></i>
                    <span>Cloud computing</span>
                </li>
                <li className="hobby"><i className="fab fa-twitch"></i>
                    <span>Live streaming</span>
                </li>
                <li className="hobby"><i className="fab fa-unity"></i>
                    <span>Game dev (Unity3D and UE)</span>
                </li>
            </ul>
        </div>
    );
};

export default Hobbies;